@import "../../vars.scss";

.error-404 {

    .error-message {
        font-size: 60px;
        margin: 100px 0 50px;
        color: red;
    }

    .error-redirect-link {
        font-size: 30px;
        color: $color-primary;
        text-decoration: none;

        &:hover,
        &:focus {
            border-bottom: 1px solid $color-primary;
        }
    }
}